
.addDoc[data-v-a2b2b9d6]{
    height: 100%;
	width: 100%;
    max-width: 900px;
    margin: 0 auto;
	background: white;
    padding: 25px;
}
.header[data-v-a2b2b9d6]{
    margin-top: 0;
	margin-bottom: 30px;
}
/* Поля ввода */
.filename[data-v-a2b2b9d6], textarea[data-v-a2b2b9d6]{
	border: 0;
	margin: 0 auto;
    padding: 10px;
    font-size: 13.5px;
    min-width: 900px;
    max-width: 900px;
    background: rgb(223, 243, 253);
}
select[data-v-a2b2b9d6]{
    margin-right: 10px;
    padding: 6px;
    font-size: 13.5px;
    min-width: 250px;
}
.selectedUser[data-v-a2b2b9d6]{
    display:inline-block;
    margin: 0;
    padding:0;
    vertical-align:top;
    font-weight: 400;
}
.selectedUserWithCancel[data-v-a2b2b9d6]{
    background: rgb(223, 243, 253);
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 5px;
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-rows: auto;
}
.selectedUserWithCancel[data-v-a2b2b9d6]:hover{
    background: rgb(223, 243, 253);
    cursor: pointer;
}
.deleteSelectedUser[data-v-a2b2b9d6]{
    padding: 0px;
    padding-left: 5px;
}
/* Кнопки ЗАГРУЗИТЬ и СОЗДАТЬ */
button[data-v-a2b2b9d6], input[type="submit"] [type][data-v-a2b2b9d6]:not([type="checkbox"]), .fileContainer[data-v-a2b2b9d6]{
	border: 0;
	border-radius: 5px;
	padding: 8px;
    margin-top: 15px;
    margin-bottom: 15px;
	color: white;
    font-family: 'Courier New', Courier, monospace;
    font-size: 14px;
	background-color: #347090;
}
button[data-v-a2b2b9d6]:hover, a[data-v-a2b2b9d6]:hover{
    cursor: pointer;
}
/**/
form > p[data-v-a2b2b9d6]{
    margin-bottom: 5px;
}
/**/
.addDoc2Colon[data-v-a2b2b9d6]{
    display: grid;
    grid-template-columns: -webkit-max-content auto;
    grid-template-columns: max-content auto;
}
/**/
img[data-v-a2b2b9d6]{
    width: 110px;
}
/**/
input[type="checkbox"][data-v-a2b2b9d6]{
    height: 15px;
}
/**/
.disabled[data-v-a2b2b9d6]{
    pointer-events: none;
    background: rgb(133, 133, 133);
}
