
.editProfile[data-v-cce6525a]{
    height: 100%;
	width: 100%;
    max-width: 900px;
    margin: 0 auto;
	background: white;
    padding: 25px;
    padding-top: 0;
    display: grid;
    grid-template-columns: -webkit-max-content auto;
    grid-template-columns: max-content auto;
}
/* Поля ввода */
textarea[data-v-cce6525a], [type][data-v-cce6525a]:not([type="button"]):not([type="checkbox"]):not([type="file"]){
	border: 0;
	margin: 0 auto;
    height: 30px;
    padding-left: 15px;
	padding-right: 15px;
    min-width: 350px;
    background: rgb(223, 243, 253);
}
/* Кнопки ЗАГРУЗИТЬ и СОЗДАТЬ */
button[data-v-cce6525a], input[type="button"][data-v-cce6525a], .fileContainer[data-v-cce6525a]{
	border: 0;
	border-radius: 5px;
	padding: 8px;
    margin-top: 15px;
    margin-bottom: 15px;
	color: white;
    font-family: 'Courier New', Courier, monospace;
    font-size: 14px;
	background-color: #347090;
}
button[data-v-cce6525a]:hover, a[data-v-cce6525a]:hover{
    cursor: pointer;
}
/**/
form > p[data-v-cce6525a]{
    margin-bottom: 5px;
}
/**/
img[data-v-cce6525a]{
    width: 150px;
    margin: 0 auto;
}
/**/
input[type="checkbox"][data-v-cce6525a]{
    width: 15px;
    height: 15px;
    margin-left: 15px;
}
/**/
.disabled[data-v-cce6525a]{
    pointer-events: none;
    background: rgb(133, 133, 133) !important;
}
