
@import url('https://fonts.googleapis.com/css?family=El+Messiri:700&display=swap');

/* Фон */
.background[data-v-df1cf3a4]{
    background-color: #347090;
    color: #aaa9f8;
    height: 60px;
    font-family: 'El Messiri', sans-serif;
    font-weight: 700;
}

/* Основной стиль */
.header[data-v-df1cf3a4]{
    width: auto;
    height: 60px;
    max-width: 1400px;
    min-width: 100px;
	margin: auto;
    margin-top: 0;
    display: grid;
    grid-template-columns: -webkit-max-content auto -webkit-max-content;
    grid-template-columns: max-content auto max-content;
    color:white;
}

/* Логотип */
.logo[data-v-df1cf3a4]{
    margin: 14px;
    fill: white;
    color: white;
    margin-left: 30px;
    font-size: 24px;
    text-align: center;
    display: grid;
    grid-template-columns: -webkit-max-content -webkit-max-content;
    grid-template-columns: max-content max-content;
}
.logo[data-v-df1cf3a4]:hover{
    fill: #aaa9f8;
    color: #aaa9f8;
}
.logo > *[data-v-df1cf3a4]{
    margin: 0;
    margin-right: 10px;
}
.logo[data-v-df1cf3a4]:hover{
    cursor: pointer;
}

/* Добавить документ, уведомления, профиль */
.header-right[data-v-df1cf3a4]{
    display: inline-grid;
    grid-template-columns: repeat(3, -webkit-max-content);
    grid-template-columns: repeat(3, max-content);
}
.header-right > .tasks[data-v-df1cf3a4], .header-right > .messages[data-v-df1cf3a4]{
    margin: 0px;
    margin-right: 10px;
    margin-left: 10px;
    padding: 0;
}
.header-right p[data-v-df1cf3a4]{
    padding: 5px;
}

/* Профиль */
.profile[data-v-df1cf3a4]{
    margin: 10.5px;
    margin-left: 8px;
    margin-right: 20px;
    width: 38px;
    height: 38px;
    border: 0;
    background-color: white;
    border-radius: 50%;
}
.profile[data-v-df1cf3a4]:hover{
    cursor: pointer;
    -webkit-box-shadow: 0px 0px 10px 0px #aaa9f8;
            box-shadow: 0px 0px 10px 0px #aaa9f8;
}

/* Уведомления */
.messages[data-v-df1cf3a4]{
    display: inline-grid;
    grid-template-columns: repeat(2, -webkit-max-content);
    grid-template-columns: repeat(2, max-content);
}

/*  */
.registr[data-v-df1cf3a4]{
    border: 0;
    padding: 7px;
    margin: 7px;
    background-color: #7cb0c1;
    color: white;
}
.registr[data-v-df1cf3a4]:hover{
    cursor: pointer;
    background-color: #aaa9f8;
    -webkit-transition: background-color .3s ease-out;
    transition: background-color .3s ease-out;
}

/**/
.icon[data-v-df1cf3a4]{
    font-size: 24px;
    font-weight: 700;
    margin: 12.5px;
    display: grid;
    grid-template-columns: -webkit-max-content -webkit-max-content;
    grid-template-columns: max-content max-content;
}
.icon[data-v-df1cf3a4]:hover{
    cursor: pointer;
    color: #aaa9f8;
}
.icon:hover svg[data-v-df1cf3a4]{
    fill: #aaa9f8;
}
.icon div[data-v-df1cf3a4]{
    padding: 0px;
    margin: 0;
    margin-left: 2px;
}
