

























































































































































































































































/* Основной экран */
.mainAuth[data-v-8a289930]{
	margin: 0 auto;
	height: 100vh;
	display: grid;
    grid-template-columns: auto -webkit-max-content auto;
    grid-template-columns: auto max-content auto;
}
.login[data-v-8a289930]{
	display: grid;
	grid-template-rows: 1fr -webkit-max-content 2fr;
	grid-template-rows: 1fr max-content 2fr;
}
.mainAuth > *[data-v-8a289930]{
	text-align: center;
}
.auth[data-v-8a289930]{
	width: 400px;
	background: #ADE0FC;
}
/* Кнопки переключения вход и регистрация */
.switch[data-v-8a289930]{
	font-size: 26px;
	padding-top: 8px;
	padding-bottom: 8px;
}
/* Кнопки ВОЙТИ и ЗАРЕГЕСТРИРОВАТЬСЯ */
button[data-v-8a289930]{
	border: 0;
	border-radius: 5px;
	padding: 8px;
	color: white;
	background-color: #347090;
}
button[data-v-8a289930]:hover{
	cursor: pointer;
}
/* Поля ввода */
input[data-v-8a289930]{
	border: 0;
	height: 30px;
	margin: 0 auto;
	padding-left: 15px;
	padding-right: 15px;
}
/**/
.rememberPassword[data-v-8a289930]{
	padding-top: 30px;
	color: #347090;
	font-size: 14px;
}
.rememberPassword[data-v-8a289930]:hover{
	cursor: pointer;
}
