

























































































































































































































































































/* Основное */
.adm[data-v-87e9fd5e]{
    margin-left: 15px;
    padding: 45px;
    padding-top: 0;
    padding-left: 60px;
}
/* Поле ввода */
input[type="email"][data-v-87e9fd5e], input[type="text"][data-v-87e9fd5e]{
	border: 0;
	margin: 0;
    height: 35px;
    padding-left: 15px;
	padding-right: 15px;
    min-width: 350px;
    background: rgb(223, 243, 253);
}
/* Кнопка СОЗДАТЬ ПОЛЬЗОВАТЕЛЯ */
button[data-v-87e9fd5e]{
	border: 0;
    margin-top: 15px;
	border-radius: 5px;
	padding: 8px;
	color: white;
	background-color: #347090;
}
button[data-v-87e9fd5e]:hover{
	cursor: pointer;
}
/**/
input[type="checkbox"][data-v-87e9fd5e]{
    height: 15px;
    width: 15px;
    margin: 0;
    vertical-align: middle;
}
/* Картотеки */
.fileCabinet[data-v-87e9fd5e]{
    border: #347090 1px solid;
    border-bottom: 0;
    padding: 5px;
}
.editNameFileCabinet[data-v-87e9fd5e]{
    display: grid;
	grid-template-columns: 1fr auto;
}
.editNameFileCabinet2[data-v-87e9fd5e]{
    display: grid;
	grid-template-columns: 1fr auto auto;
}
/**/
table[data-v-87e9fd5e] {
    border-collapse: collapse;
    text-align: left;
}
table[data-v-87e9fd5e], th[data-v-87e9fd5e], td[data-v-87e9fd5e]{
    border: #64b2db 2px solid;
    border-radius: 5px;
}
th[data-v-87e9fd5e]{
    background: rgb(223, 243, 253);
}
td[data-v-87e9fd5e], th[data-v-87e9fd5e]{
    padding: 7px 15px;
}
.arrow[data-v-87e9fd5e]{
    display: inline-block;
}
.editAdm[data-v-87e9fd5e]{
    display: grid;
	grid-template-columns: 1fr auto;
}
.editNotif[data-v-87e9fd5e]{
    display: grid;
	grid-template-columns: 1fr auto;
}
.editStaff[data-v-87e9fd5e], .editNotif2[data-v-87e9fd5e]{
    fill: #64b2db;
    vertical-align: text-bottom;
    text-align: right;
}
.editStaff[data-v-87e9fd5e]:hover, .editNotif2[data-v-87e9fd5e]:hover{
    cursor: pointer;
    fill: #347090;
}
.fastLink a[data-v-87e9fd5e]{
    text-decoration: none;
}
/**/
hr[data-v-87e9fd5e] { 
    overflow: visible;
    border: 0;
    margin-left: -2px;
    border-top: dotted #347090 4px;
} 
