
.document[data-v-0487a1d6]{
    height: 100%;
	width: 100%;
    max-width: 900px;
    margin: 0 auto;
	background: white;
    padding: 25px;
}
.header[data-v-0487a1d6]{
    margin-top: 0;
	margin-bottom: 10px;
}
/**/
.document2Colon[data-v-0487a1d6]{
    display: grid;
    grid-template-columns: -webkit-max-content auto;
    grid-template-columns: max-content auto;
	margin-top: 20px;
}
/**/
button[data-v-0487a1d6], .button[data-v-0487a1d6]{
	width: auto;
	border: 0;
	border-radius: 5px;
	padding: 8px;
    margin-top: 5px;
    margin-bottom: 5px;
	color: white;
    font-family: 'Courier New', Courier, monospace;
    font-size: 14px;
	background-color: #347090;
	text-align: center;
}
.buttons[data-v-0487a1d6]{
	max-width: 900px;
}
.signButtons[data-v-0487a1d6]{
	display: grid;
    grid-template-columns: 1fr auto auto 1fr;
}
.signButtons button[data-v-0487a1d6]{
	margin: 10px;
	margin-top: 5px;
	margin-bottom: 20px;
}
.confirmButtons[data-v-0487a1d6]{
	display: grid;
    grid-template-columns: 1fr auto 1fr;
}
.confirmButtons button[data-v-0487a1d6]{
	margin: 10px;
	margin-top: 25px;
}
a[data-v-0487a1d6]{
	color: white;
	text-decoration: none;
}
button[data-v-0487a1d6]:hover, .button[data-v-0487a1d6]:hover{
	cursor: pointer;
}
/* Подтверждение */
.confirm[data-v-0487a1d6]{
	position: fixed;
	top: 20%;
	left: calc(50% - 200px);
	margin: 0;
	width: 400px;
	background: rgb(223, 243, 253);
	border: 1px solid #347090;
}
.notAddHeight[data-v-0487a1d6]{
	height: 240px;
}
.addHeight[data-v-0487a1d6]{
	height: 300px;
}
.confirm p[data-v-0487a1d6]{
	padding: 20px;
	padding-bottom: 10px;
	text-align: justify;
}
.code[data-v-0487a1d6]{
	border: 0;
	margin: 0 auto;
	margin-left: 20px;
	margin-right: 20px;
    padding: 10px;
    font-size: 13.5px;
    width: calc(100% - 60px);
    background: white;
	color: black;
}
/* Причина отклонения, комментарий */
textarea[data-v-0487a1d6]{
	border: 0;
	margin: 0 15px;
    padding: 10px;
    font-size: 13.5px;
    min-width: 350px;
	max-width: 350px;
	min-height: 70px;
	max-height: 70px;
    background: white;
}
/* Файл, который загружается при отклонении подписи*/
.cancelFile[data-v-0487a1d6]{
	margin: 15px;
	margin-bottom: 0;
}
/* Таблица */
table[data-v-0487a1d6] {
    border-collapse: collapse;
}
table[data-v-0487a1d6], tr[data-v-0487a1d6]{
    border: rgb(223, 243, 253) 2px solid;
	border-left: 0;
	border-right: 0;
    border-radius: 5px;
}
td[data-v-0487a1d6]{
    padding: 7px 15px;
}
